import { ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import axios from "axios";
import png from "@/assets/chatGpt.jpg";
import { ElMessage } from 'element-plus';
export default {
  setup() {
    const router = useRouter();
    const username = ref("");
    const password = ref("");
    const checked = ref("");
    const image = png;
    onMounted(() => {
      const savedUsername = localStorage.getItem("savedUsername");
      const savedPassword = localStorage.getItem("savedPassword");
      const savedRemember = localStorage.getItem("savedRemember");
      if (savedRemember === "true") {
        username.value = savedUsername || "";
        password.value = savedPassword || "";
        checked.value = true;
      }
    });
    const submit = () => {
      // 处理登录逻辑
      if (checked.value) {
        // 如果记住密码被选中，将用户名和密码保存到本地存储
        localStorage.setItem("savedUsername", username.value);
        localStorage.setItem("savedPassword", password.value);
        localStorage.setItem("savedRemember", "true");
      } else {
        // 如果不记住密码，清除本地存储中的信息
        localStorage.removeItem("savedUsername");
        localStorage.removeItem("savedPassword");
        localStorage.removeItem("savedRemember");
      }
      axios.post(`/api/login?userName=${username.value}&password=${password.value}`).then(response => {
        if (response.data.code == 1) {
          console.log("登录成功", response.data.data);
          const token = response.data.data;
          localStorage.setItem("jwtToken", token);
          ElMessage('登录成功！');
          setTimeout(() => {
            router.replace("/");
          }, 1000);
        } else {
          console.error("登录失败");
          ElMessage('账号或密码错误！');
        }
      }).catch(error => {
        console.error("登录时出现错误:", error);
        ElMessage('账号或密码错误！');
      });
    };
    return {
      username,
      password,
      image,
      checked,
      submit
    };
  }
};